.arrow-6 {
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  display: flex;
}

.arrow-6 svg {
  width: 45px;
  height: auto;
  cursor: pointer;
  overflow: visible;
}

.arrow-6 svg polygon, .arrow-6 svg path {
  fill: #fff;
  transition: all .5s cubic-bezier(.2, 1, .3, 1);
}

.arrow-6 svg:hover polygon, .arrow-6 svg:hover path {
  fill: #f34d00;
  transition: all 1s cubic-bezier(.2, 1, .3, 1);
}

.arrow-6 svg:hover .arrow-6-pl {
  animation: arrow-6-anim 1s cubic-bezier(.2, 1, .3, 1) infinite;
}

.arrow-6 svg:hover .arrow-6-pl-fixed {
  animation: arrow-6-fixed-anim 1s cubic-bezier(.2, 1, .3, 1) infinite;
}

@keyframes arrow-6-anim {
  0% {
    opacity: 1;
    transform: translateX(0);
  }

  5% {
    transform: translateX(-.1rem);
  }

  100% {
    opacity: 0;
    transform: translateX(1rem);
  }
}

@keyframes arrow-6-fixed-anim {
  5% {
    opacity: 0;
  }

  20% {
    opacity: .4;
  }

  100% {
    opacity: 1;
  }
}

